<template>
    <div class="game">
      <h1>cuilintestcuilintest</h1>
      <div style="margin: 0 auto;width: 1200px;">
        <vueUEditor @ready="editorReady" ></vueUEditor>
        <div v-html="data" style="border: 1px solid; height: 300px;margin-top: 50px;padding: 0 10px;"></div>
      </div>
    </div>
</template>

<style>

</style>

<script>
    import VueUEditor from 'vue-ueditor'
    export default {
        data: function () {
            return {
              data:''
            }
        },
        components: {
          VueUEditor
        },
        computed: {},
        watch: {},
        created: function () {
          console.log(1)
        },
        methods: {
          editorReady:function (editorInstance) {
            editorInstance.setContent('gogogo')
            this.data = editorInstance.getContent()
            editorInstance.addListener('contentChange',() => {
              console.log('变化如下：',editorInstance.getContent())
              this.data = editorInstance.getContent()
            })
          }
        },
        mounted: function () {

        }
    }
</script>



