import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import editor from '../view/demo.vue'
import cuilintest from '../view/cuilintest.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/editor',
      name: 'editor',
      component: editor
    },
    {
      path: '/cuilintest',
      name: 'cuilintest',
      component: cuilintest
    }

  ]
})



